* {
  margin: 0;
  padding: 0;
  text-decoration: none;

  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#root {
  display: flex;
  align-items: flex-start;
}

canvas {
  border: 5px solid #000;
  background-image: url("./images/peristil.jpg");
  background-size: 100%;
}

.arrow {
  display: inline-block;
  transform: rotate(90deg);
  border: 5px solid #000;
  height: 30px;
  width: 30px;
  text-align: center;
  font-size: 20px;
  margin-bottom: 10px;
}

.brush-width {
  width: 52px;
  height: 30px;
  font-size: 30px;
  animation: easeIn 0.75s;
}

.hue-picker {
  padding: 0;
  margin-top: 10px;
  animation: easeIn 1.25s;
}

.hue-picker > div > div > div > div {
  width: 22px !important;
  height: 22px !important;
}

@keyframes easeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
